html {
    box-sizing: border-box;
}

* {
    margin: 0;
    padding: 0;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    padding: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    /* background-color: #000000; */
    background-image: url(/Build-a-Drum-Machine-freeCodeCamp/static/media/music1.c7de48af.jpg);
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.container {
    /* outline: 5px solid #ffffff; */
    box-shadow: black 3px 3px 5px;
    position: relative;
    width: 660px;
    border-radius: 6px;
    text-align: center;
    background-color:  #01ffcc;
}

#root {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100vh;
}

.key {
    position: relative;
    float: left;
    transition: all .07s ease;
    color : white;
    width: 100px;
    height: 80px;
    margin-right: 10px;
    border-radius: 5px;
    padding-top: 35px;
    box-sizing: border-box;
    cursor: pointer;
    background-color: rgb(0, 0, 0);
    margin-top: 10px;
    box-shadow: black 3px 3px 5px;
}

.btnDrum {
    width: 332px;
    height: 272px;
    display: inline-block;
    margin: 20px;
}

.bankController {
    width: 240px;
    height: 272px;
    display: inline-block;
    margin: 40px 20px 0 10px;
    vertical-align: top;
}

.power {
    width: 100px;
    margin: auto;
}

.powerChange {
    width: 23px;
    height: 16px;
    background: #1ab394;
    border: 1px solid black;
    box-sizing: border-box;
    cursor: pointer;
}

.shaker {
    width: 200px;
    background-color: rgb(5, 5, 5);
    color: #ffffff;
    margin: 15px auto;
    padding: 15px;
    box-sizing: border-box;
}

input [type=range] {
    -webkit-appearance: none;
    margin: 12.5px 0;
    width: 100%;
}

.select {
    margin: auto;
    border: 1px solid black;
    width: 50px;
    height: 20px;
    padding: 1px;
    background-color: black;
}

@media screen and (max-width: 650px) {
    .container {
        -webkit-transform: scale(0.7);
                transform: scale(0.7);
    }
}

.logo {
    position: absolute;
    top: 5px;
    right: 10px;
}

.textLogo {
    font-weight: bold;
}

.playing {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    border-color: #1ab394;
    box-shadow: 0 0 1rem #1ab394;
}


/* Range input */

input[type=range] {
    -webkit-appearance: none;
    width: 100%;
    margin: 13.8px 0;
}

input[type=range]:focus {
    outline: none;
}

input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    background: #000000;
    border-radius: 1.3px;
    border: 0.2px solid #010101;
}

input[type=range]::-webkit-slider-thumb {
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    border: 1px solid #000000;
    height: 26px;
    width: 16px;
    border-radius: 3px;
    background: #1ab394;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -9px;
}

input[type=range]:focus::-webkit-slider-runnable-track {
    background: #1ab394;
}

input[type=range]::-moz-range-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    background: #1ab394;
    border-radius: 1.3px;
    border: 0.2px solid #010101;
}

input[type=range]::-moz-range-thumb {
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    border: 1px solid #000000;
    height: 36px;
    width: 16px;
    border-radius: 3px;
    background: #ffffff;
    cursor: pointer;
}

input[type=range]::-ms-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    background: transparent;
    border-color: transparent;
    color: transparent;
}

input[type=range]::-ms-fill-lower {
    background: #1ab394;
    border: 0.2px solid #010101;
    border-radius: 2.6px;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}

input[type=range]::-ms-fill-upper {
    background: #1ab394;
    border: 0.2px solid #010101;
    border-radius: 2.6px;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}

input[type=range]::-ms-thumb {
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    border: 1px solid #000000;
    height: 26px;
    width: 16px;
    border-radius: 3px;
    background: #ffffff;
    cursor: pointer;
    height: 8.4px;
}

input[type=range]:focus::-ms-fill-lower {
    background: #1ab394;
}

input[type=range]:focus::-ms-fill-upper {
    background: #1ab394;
}


